#skills-wrapper {
	display: flex;
	justify-content: space-evenly;
	flex-wrap: wrap;
	margin-left: 45px;
	margin-right: 45px;
}

#javascript {
	background-color: rgb(240, 219, 79);
	color: black;
}

#js-pic {
	background-image: url('https://upload.wikimedia.org/wikipedia/commons/6/6a/JavaScript-logo.png');
	background-repeat: no-repeat;
	background-size: cover;
	position: absolute;
	margin-top: 200px;
	margin-left: 100px;
	width: 200px;
	height: 200px;
	z-index: -1;
}

#node {
	background-color: rgb(128, 189, 3);
	color: black;
}

#node-pic {
	background-image: url('https://colorlib.com/wp/wp-content/uploads/sites/2/node.js-logo.png');
	background-repeat: no-repeat;
	background-size: cover;
	position: absolute;
	margin-top: 250px;
	width: inherit;
	height: 150px;
	z-index: -1;
}

#go {
	background-color: rgb(105, 215, 226);
	color: black;
}

#go-pic {
	background-image: url('https://i.ya-webdesign.com/images/golang-vector-5.png');
	background-repeat: no-repeat;
	background-size: cover;
	position: absolute;
	margin-top: 250px;
	width: inherit;
	height: 150px;
	z-index: -1;
}

#sql {
	background-color: rgb(51, 103, 145);
	color: white;
}

#sql-pic {
	background-image: url('https://upload.wikimedia.org/wikipedia/commons/thumb/2/29/Postgresql_elephant.svg/1200px-Postgresql_elephant.svg.png');
	background-repeat: no-repeat;
	background-size: contain;
	position: absolute;
	overflow: hidden;
	margin-top: 260px;
	margin-left: 50px;
	width: inherit;
	height: 180px;
	z-index: -1;
}

#vba {
	background-color: whitesmoke;
	color: black;
}

#vba-pic {
	background-image: url('https://i0.wp.com/beatexcel.com/wp-content/uploads/2013/07/vba-logo.png?resize=572%2C217');
	background-repeat: no-repeat;
	background-size: contain;
	position: absolute;
	margin-top: 250px;
	width: inherit;
	height: 150px;
	z-index: -1;
}

#deployment {
	color: white;
}

#deploy-pic {
	background-image: url('https://image.flaticon.com/icons/svg/25/25231.svg');
	background-repeat: no-repeat;
	background-size: contain;
	background-position: center;
	position: absolute;
	width: inherit;
	height: 440px;
	z-index: -1;
}

.skills-column {
	height: 440px;
	z-index: 1;
	margin: 10px;
	width: 300px;
	max-width: 500px;
	display: flex;
	border-radius: 10px;
	flex-direction: column;
	padding: 10px;
	background: rgb(68, 72, 87, 0.8);
	color: whitesmoke;
}

.skills-header {
	padding: 5px;
	display: flex;
	font-size: 18pt;
	font-weight: bold;
	justify-content: center;
	border-bottom: solid 3px whitesmoke;
	border-right: solid 5px whitesmoke;
	--notchSize: 25px;
	-webkit-clip-path: polygon(
		0% 0%,
		0% 0%,
		calc(100% - var(--notchSize)) 0%,
		100% var(--notchSize),
		100% calc(100% - var(--notchSize)),
		100% 100%,
		var(--notchSize) 100%,
		0% calc(100% - var(--notchSize))
	);
	clip-path: polygon(
		0% 0%,
		0% 0%,
		calc(100% - var(--notchSize)) 0%,
		100% var(--notchSize),
		100% calc(100% - var(--notchSize)),
		100% 100%,
		var(--notchSize) 100%,
		0% calc(100% - var(--notchSize))
	);
}

.skills {
	padding: 5px;
	padding-left: 25px;
	margin-top: 5px;
}

.skill-detail-wrapper {
	display: flex;
	flex-direction: column;
	margin-top: 10px;
	height: 100%;
}
